.post {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  background-color: yellowgreen;
}

.postAvatar {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.postContainer {
  display: flex;
  align-items: center;
}
